'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { Search, Filter, Calendar, User, Eye, Heart, Clock, Tag, ChevronRight, TrendingUp, Star } from 'lucide-react';

// Mock data - Replace with actual API call
const mockBlogs = [
  {
    _id: '1',
    title: 'गीता के अनमोल वचन: जीवन में शांति के लिए',
    content: 'भगवद गीता के माध्यम से जीवन में शांति और खुशी पाने के उपाय...',
    excerpt: 'भगवद गीता के श्लोकों से सीखें कि कैसे जीवन में शांति और संतुष्टि पाई जा सकती है।',
    author: {
      name: 'राम शर्मा',
      avatar: '/avatar1.jpg'
    },
    featuredImage: '/blog1.jpg',
    category: 'आध्यात्म',
    tags: ['गीता', 'शांति', 'जीवन'],
    publishedAt: '2024-01-15',
    readingTime: '5 मिनट',
    views: 1250,
    likes: 89,
    featured: true,
    status: 'published'
  },
  {
    _id: '2',
    title: 'योग और मेडिटेशन: स्वस्थ जीवन का आधार',
    content: 'प्राचीन योग विद्या से आधुनिक जीवन में स्वास्थ्य और खुशी...',
    excerpt: 'योग और मेडिटेशन की शक्ति से अपने जीवन को कैसे बेहतर बनाएं।',
    author: {
      name: 'सुनीता देवी',
      avatar: '/avatar2.jpg'
    },
    featuredImage: '/blog2.jpg',
    category: 'योग',
    tags: ['योग', 'मेडिटेशन', 'स्वास्थ्य'],
    publishedAt: '2024-01-12',
    readingTime: '7 मिनट',
    views: 980,
    likes: 65,
    featured: false,
    status: 'published'
  },
  {
    _id: '3',
    title: 'वेदांत दर्शन: सत्य की खोज',
    content: 'वेदांत के माध्यम से जीवन के मूलभूत प्रश्नों के उत्तर...',
    excerpt: 'वेदांत दर्शन के गहरे सिद्धांतों को समझें और जीवन में अपनाएं।',
    author: {
      name: 'आचार्य गुप्ता',
      avatar: '/avatar3.jpg'
    },
    featuredImage: '/blog3.jpg',
    category: 'दर्शन',
    tags: ['वेदांत', 'दर्शन', 'ज्ञान'],
    publishedAt: '2024-01-10',
    readingTime: '10 मिनट',
    views: 756,
    likes: 45,
    featured: true,
    status: 'published'
  }
];

const categories = ['सभी', 'आध्यात्म', 'योग', 'दर्शन', 'त्योहार', 'संस्कृति', 'इतिहास'];

export default function BlogsPage() {
  const [blogs, setBlogs] = useState(mockBlogs);
  const [filteredBlogs, setFilteredBlogs] = useState(mockBlogs);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('सभी');
  const [currentPage, setCurrentPage] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const blogsPerPage = 6;

  // Filter blogs based on search and category
  useEffect(() => {
    let filtered = blogs;

    if (searchTerm) {
      filtered = filtered.filter(blog =>
        blog.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        blog.excerpt.toLowerCase().includes(searchTerm.toLowerCase()) ||
        blog.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()))
      );
    }

    if (selectedCategory !== 'सभी') {
      filtered = filtered.filter(blog => blog.category === selectedCategory);
    }

    setFilteredBlogs(filtered);
    setCurrentPage(1);
  }, [searchTerm, selectedCategory, blogs]);

  // Pagination
  const indexOfLastBlog = currentPage * blogsPerPage;
  const indexOfFirstBlog = indexOfLastBlog - blogsPerPage;
  const currentBlogs = filteredBlogs.slice(indexOfFirstBlog, indexOfLastBlog);
  const totalPages = Math.ceil(filteredBlogs.length / blogsPerPage);

  const featuredBlogs = blogs.filter(blog => blog.featured);

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('hi-IN', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-orange-50 via-white to-yellow-50">
      {/* SEO Meta Tags */}
      <head>
        <title>सनातन ब्लॉग्स - आध्यात्मिक ज्ञान और जीवन दर्शन</title>
        <meta name="description" content="सनातन धर्म, योग, मेडिटेशन, और आध्यात्मिक ज्ञान पर बेहतरीन लेख पढ़ें। जीवन को बेहतर बनाने के लिए प्राचीन भारतीय दर्शन का अध्ययन करें।" />
        <meta name="keywords" content="सनातन धर्म, योग, मेडिटेशन, आध्यात्म, गीता, वेदांत, भारतीय दर्शन" />
        <meta name="robots" content="index, follow" />
        <meta property="og:title" content="सनातन ब्लॉग्स - आध्यात्मिक ज्ञान और जीवन दर्शन" />
        <meta property="og:description" content="प्राचीन भारतीय ज्ञान और आधुनिक जीवन को जोड़ने वाले गहरे लेख" />
        <meta property="og:type" content="website" />
        <link rel="canonical" href="/blogs" />
      </head>

      {/* Hero Section */}
      <section className="relative bg-gradient-to-r from-orange-600 to-orange-800 text-white py-20 overflow-hidden">
        <div className="absolute inset-0 bg-black opacity-20"></div>
        <div className="absolute top-0 left-0 w-full h-full">
          <div className="absolute top-10 left-10 w-20 h-20 bg-yellow-300 rounded-full mix-blend-multiply filter blur-xl opacity-70 animate-blob"></div>
          <div className="absolute top-0 right-0 w-32 h-32 bg-orange-300 rounded-full mix-blend-multiply filter blur-xl opacity-70 animate-blob animation-delay-2000"></div>
          <div className="absolute -bottom-8 left-20 w-24 h-24 bg-yellow-400 rounded-full mix-blend-multiply filter blur-xl opacity-70 animate-blob animation-delay-4000"></div>
        </div>
        
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-6">
            सनातन <span className="text-yellow-300">ब्लॉग्स</span>
          </h1>
          <p className="text-xl md:text-2xl mb-8 text-orange-100 max-w-3xl mx-auto">
            प्राचीन भारतीय ज्ञान और आधुनिक जीवन को जोड़ने वाले गहरे लेख पढ़ें
          </p>
          <div className="flex items-center justify-center space-x-2 text-yellow-200">
            <Star className="h-5 w-5 fill-current" />
            <span className="text-lg">आध्यात्मिक यात्रा शुरू करें</span>
            <Star className="h-5 w-5 fill-current" />
          </div>
        </div>
      </section>

      {/* Search and Filter Section */}
      <section className="py-12 bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
            {/* Search Bar */}
            <div className="relative flex-1 max-w-lg">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Search className="h-5 w-5 text-gray-400" />
              </div>
              <input
                type="text"
                placeholder="लेख खोजें..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="block w-full pl-10 pr-3 py-3 border-2 border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent transition-all duration-300"
              />
            </div>

            {/* Category Filter */}
            <div className="flex items-center space-x-2">
              <Filter className="h-5 w-5 text-gray-600" />
              <div className="flex flex-wrap gap-2">
                {categories.map((category) => (
                  <button
                    key={category}
                    onClick={() => setSelectedCategory(category)}
                    className={`px-4 py-2 rounded-full text-sm font-medium transition-all duration-300 ${
                      selectedCategory === category
                        ? 'bg-orange-600 text-white shadow-lg transform scale-105'
                        : 'bg-gray-100 text-gray-700 hover:bg-orange-100 hover:text-orange-600'
                    }`}
                  >
                    {category}
                  </button>
                ))}
              </div>
            </div>
          </div>

          {/* Results Count */}
          <div className="mt-6 text-gray-600">
            <span className="font-medium">{filteredBlogs.length}</span> लेख मिले
          </div>
        </div>
      </section>

      {/* Featured Blogs Section */}
      {featuredBlogs.length > 0 && (
        <section className="py-16 bg-gradient-to-r from-orange-50 to-yellow-50">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="text-center mb-12">
              <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
                <TrendingUp className="inline-block h-8 w-8 text-orange-600 mr-2" />
                फीचर्ड लेख
              </h2>
              <p className="text-lg text-gray-600">सबसे लोकप्रिय और महत्वपूर्ण लेख</p>
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-8">
              {featuredBlogs.map((blog) => (
                <article
                  key={blog._id}
                  className="bg-white rounded-2xl shadow-xl overflow-hidden hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-2"
                >
                  <div className="relative h-48 bg-gradient-to-r from-orange-400 to-orange-600">
                    <div className="absolute inset-0 bg-black opacity-20"></div>
                    <div className="absolute top-4 left-4">
                      <span className="bg-yellow-400 text-gray-900 px-3 py-1 rounded-full text-xs font-bold">
                        ⭐ फीचर्ड
                      </span>
                    </div>
                    <div className="absolute bottom-4 left-4 right-4 text-white">
                      <span className="bg-orange-600 px-2 py-1 rounded-full text-xs font-medium">
                        {blog.category}
                      </span>
                    </div>
                  </div>

                  <div className="p-6">
                    <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2 hover:text-orange-600 transition-colors">
                      <Link href={`/blogs/${blog._id}`}>
                        {blog.title}
                      </Link>
                    </h3>

                    <p className="text-gray-600 mb-4 line-clamp-3">
                      {blog.excerpt}
                    </p>

                    <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
                      <div className="flex items-center space-x-4">
                        <span className="flex items-center">
                          <Calendar className="h-4 w-4 mr-1" />
                          {formatDate(blog.publishedAt)}
                        </span>
                        <span className="flex items-center">
                          <Clock className="h-4 w-4 mr-1" />
                          {blog.readingTime}
                        </span>
                      </div>
                    </div>

                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-orange-100 rounded-full flex items-center justify-center">
                          <span className="text-sm font-medium text-orange-600">
                            {blog.author.name.charAt(0)}
                          </span>
                        </div>
                        <span className="text-sm font-medium text-gray-700">{blog.author.name}</span>
                      </div>

                      <Link
                        href={`/blogs/${blog._id}`}
                        className="inline-flex items-center text-orange-600 hover:text-orange-700 font-medium"
                      >
                        पढ़ें
                        <ChevronRight className="h-4 w-4 ml-1" />
                      </Link>
                    </div>
                  </div>
                </article>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* All Blogs Section */}
      <section className="py-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-gray-900 mb-4">
              सभी लेख
            </h2>
            <p className="text-lg text-gray-600">ज्ञान की गहराई में जाने के लिए अन्य लेख भी देखें</p>
          </div>

          {isLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {[...Array(6)].map((_, i) => (
                <div key={i} className="bg-white rounded-2xl shadow-lg overflow-hidden animate-pulse">
                  <div className="h-48 bg-gray-200"></div>
                  <div className="p-6">
                    <div className="h-4 bg-gray-200 rounded mb-4"></div>
                    <div className="h-4 bg-gray-200 rounded mb-4"></div>
                    <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                  </div>
                </div>
              ))}
            </div>
          ) : currentBlogs.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
              {currentBlogs.map((blog) => (
                <article
                  key={blog._id}
                  className="bg-white rounded-2xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 transform hover:-translate-y-1"
                >
                  <div className="relative h-48 bg-gradient-to-r from-gray-400 to-gray-600">
                    <div className="absolute inset-0 bg-black opacity-20"></div>
                    <div className="absolute top-4 left-4">
                      <span className="bg-white bg-opacity-90 text-gray-900 px-2 py-1 rounded-full text-xs font-medium">
                        {blog.category}
                      </span>
                    </div>
                    <div className="absolute bottom-4 left-4 right-4">
                      <div className="flex flex-wrap gap-1">
                        {blog.tags.slice(0, 2).map((tag) => (
                          <span
                            key={tag}
                            className="bg-orange-600 text-white px-2 py-1 rounded text-xs"
                          >
                            #{tag}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>

                  <div className="p-6">
                    <h3 className="text-xl font-bold text-gray-900 mb-3 line-clamp-2 hover:text-orange-600 transition-colors">
                      <Link href={`/blogs/${blog._id}`}>
                        {blog.title}
                      </Link>
                    </h3>

                    <p className="text-gray-600 mb-4 line-clamp-3">
                      {blog.excerpt}
                    </p>

                    <div className="flex items-center justify-between text-sm text-gray-500 mb-4">
                      <span className="flex items-center">
                        <Calendar className="h-4 w-4 mr-1" />
                        {formatDate(blog.publishedAt)}
                      </span>
                      <span className="flex items-center">
                        <Clock className="h-4 w-4 mr-1" />
                        {blog.readingTime}
                      </span>
                    </div>

                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <div className="w-8 h-8 bg-orange-100 rounded-full flex items-center justify-center">
                          <span className="text-sm font-medium text-orange-600">
                            {blog.author.name.charAt(0)}
                          </span>
                        </div>
                        <span className="text-sm font-medium text-gray-700">{blog.author.name}</span>
                      </div>

                      <div className="flex items-center space-x-2 text-xs text-gray-500">
                        <span className="flex items-center">
                          <Eye className="h-3 w-3 mr-1" />
                          {blog.views}
                        </span>
                        <span className="flex items-center">
                          <Heart className="h-3 w-3 mr-1" />
                          {blog.likes}
                        </span>
                      </div>
                    </div>

                    <div className="mt-4 pt-4 border-t border-gray-100">
                      <Link
                        href={`/blogs/${blog._id}`}
                        className="w-full bg-orange-600 text-white py-2 px-4 rounded-xl hover:bg-orange-700 transition-colors font-medium text-center block"
                      >
                        पूरा लेख पढ़ें
                      </Link>
                    </div>
                  </div>
                </article>
              ))}
            </div>
          ) : (
            <div className="text-center py-16">
              <div className="text-6xl mb-4">🔍</div>
              <h3 className="text-2xl font-bold text-gray-900 mb-4">कोई लेख नहीं मिला</h3>
              <p className="text-gray-600 mb-8">कृपया अपनी खोज या फिल्टर बदलने का प्रयास करें</p>
              <button
                onClick={() => {
                  setSearchTerm('');
                  setSelectedCategory('सभी');
                }}
                className="bg-orange-600 text-white px-6 py-3 rounded-xl hover:bg-orange-700 transition-colors"
              >
                सभी लेख देखें
              </button>
            </div>
          )}

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="mt-12 flex justify-center">
              <nav className="flex items-center space-x-2">
                <button
                  onClick={() => setCurrentPage(currentPage - 1)}
                  disabled={currentPage === 1}
                  className={`px-4 py-2 rounded-lg ${
                    currentPage === 1
                      ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                      : 'bg-white text-gray-700 hover:bg-orange-50 border border-gray-200'
                  }`}
                >
                  पिछला
                </button>

                {[...Array(totalPages)].map((_, i) => (
                  <button
                    key={i + 1}
                    onClick={() => setCurrentPage(i + 1)}
                    className={`px-4 py-2 rounded-lg ${
                      currentPage === i + 1
                        ? 'bg-orange-600 text-white'
                        : 'bg-white text-gray-700 hover:bg-orange-50 border border-gray-200'
                    }`}
                  >
                    {i + 1}
                  </button>
                ))}

                <button
                  onClick={() => setCurrentPage(currentPage + 1)}
                  disabled={currentPage === totalPages}
                  className={`px-4 py-2 rounded-lg ${
                    currentPage === totalPages
                      ? 'bg-gray-100 text-gray-400 cursor-not-allowed'
                      : 'bg-white text-gray-700 hover:bg-orange-50 border border-gray-200'
                  }`}
                >
                  अगला
                </button>
              </nav>
            </div>
          )}
        </div>
      </section>

      {/* Newsletter Section */}
      <section className="py-16 bg-gradient-to-r from-orange-600 to-orange-800 text-white">
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl md:text-4xl font-bold mb-4">
            नवीनतम लेख सीधे अपने ईमेल में पाएं
          </h2>
          <p className="text-xl text-orange-100 mb-8">
            हमारे न्यूज़लेटर की सदस्यता लें और आध्यात्मिक ज्ञान से भरपूर लेख नियमित रूप से पाएं
          </p>
          <div className="max-w-md mx-auto flex gap-4">
            <input
              type="email"
              placeholder="आपका ईमेल एड्रेस"
              className="flex-1 px-4 py-3 rounded-xl text-gray-900 focus:outline-none focus:ring-2 focus:ring-yellow-400"
            />
            <button className="bg-yellow-400 text-gray-900 px-6 py-3 rounded-xl font-bold hover:bg-yellow-300 transition-colors">
              सदस्यता लें
            </button>
          </div>
        </div>
      </section>

      {/* Custom CSS for animations */}
      <style jsx>{`
        @keyframes blob {
          0% {
            transform: translate(0px, 0px) scale(1);
          }
          33% {
            transform: translate(30px, -50px) scale(1.1);
          }
          66% {
            transform: translate(-20px, 20px) scale(0.9);
          }
          100% {
            transform: translate(0px, 0px) scale(1);
          }
        }
        .animate-blob {
          animation: blob 7s infinite;
        }
        .animation-delay-2000 {
          animation-delay: 2s;
        }
        .animation-delay-4000 {
          animation-delay: 4s;
        }
        .line-clamp-2 {
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        .line-clamp-3 {
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      `}</style>
    </div>
  );
} 